import React, { useContext, useState } from 'react';
import './task.css';
import { TaskContext } from './contexts/taskContext';

export default function Tasks (){
    const contextType = useContext(TaskContext);
    const {tasks, deleteTask, updatingTask} = contextType;
    const [edit, setEdit] = useState(false);
    const [taskVal, setTaskVal] = useState({"node":{"id":"", "title":"", "completed":""}})
    let titleVal = document.getElementById('title');
      
    function strikeUnstrike(task){
      updatingTask(task, true);
    };

    function startEdit(task){
      titleVal.value = task.node.title
      setTaskVal(task);
      setEdit(true);
    };
    
    // when submit is clicked
    function handleSubmit(event){
      event.preventDefault(); 
      // eslint-disable-next-line
      if(!titleVal.value.replace(/\s/g,"") == ""){ // just making all the space to one space and check wheather it is a value or empty string
        if(edit){
          taskVal.node.title = titleVal.value;
          updatingTask(taskVal, false);  // false we do for means that this is not strike
          titleVal.value='';
          setEdit(false);
        }else{
          const {createTask} = contextType;
          createTask(titleVal.value);
          titleVal.value = ''
        }  
      }
    };

    return (
    <>
      <div style={{display:"flex", position:"relative", height:"inherit", width:"100%", justifyContent:"center"}} id="particles-js">
        <div style={{position:"absolute"}} className="container">
          <div id="task-container">
            <div id="form-wrapper">
              <form id="form" onSubmit={handleSubmit}>
                <div className="flex-wrapper">
                  <div style={{flex: 6}}>
                    <input  className="form-control" id="title" type="text" name="title" placeholder="Add task.."/>
                  </div>
                  <div style={{flex: 1}}>
                    <input id="submit" className="btn btn-warning" type="submit" name="Add" />
                  </div>
                </div>
              </form>
            </div>

            <div id="list-wrapper">
                {tasks.map(function(task){
                  return(
                    <div key={task.node.id} className="task-wrapper flex-wrapper">
                      
                      <div onClick={() => strikeUnstrike(task)} style={{flex:7}}> {/* So here we write the condtional control statement */}
                        {task.node.completed === false ? (<span>{task.node.title}</span>) : (<strike>{task.node.title}</strike>) }
                      </div>
                      
                      <div style={{flex:1}}>
                        <button onClick={() => startEdit(task)} className="btn btn-sm btn-outline-info">Edit</button>
                      </div>
                      
                      <div style={{flex:1}}>
                        <button onClick={() => deleteTask(task)} className="btn btn-sm btn-outline-dark delete"><i style={{color:"inherit"}} className="fa fa-trash"></i></button>
                      </div>

                    </div>  
                  );
                })}
            </div>

          </div>    
        </div>
      </div>
    </>       
    );
}

